import React from 'react';
import Slider from 'react-slick';
import ImageLoader from 'react-imageloader';
import MediaQuery from 'react-responsive';
import RaisedButton from 'material-ui/lib/raised-button';

const styleraisedbutton = {
  textAlign: 'center'
};

class Carousel extends React.Component {
  render() {
    const settings = {
      dots: false,
      infinite: true,
      speed: 6000,
      autoplaySpeed: 3000,
      autoplay: true,
      arrows: false,
      adaptiveHeight: false,
      fade: true,
      centerMode: false,
      draggable: false
    };
    return (
      <div className ="carouselscroll">
        <Slider {...settings}>
          <div className = "carousel1">
            <h3> Hey, we are </h3> 
            <MediaQuery query='(max-width: 321px)'>
              <img className="image101" src="../resources/NewAppendLogoV2SoloSmall.svg"/>
            </MediaQuery>
            <MediaQuery query='(min-width: 322px)'>
              <img className="image101" src="../resources/NewAppendLogoV2Solo.svg"/>
            </MediaQuery>
            <br></br>
            <h3> Premium Small Business Apps </h3>
            <br></br>
            <RaisedButton label="Lets Talk" secondary={true} linkButton ={true} href="https://tomasrbaronas.typeform.com/to/IxD3Rq" style = {styleraisedbutton} /> 
          </div>
          <div className = "carousel2">
            <MediaQuery query='(min-width: 700px)'>
              <img src="../resources/AndroidAngledEntry.png" className="image102"/>
            </MediaQuery>
            <MediaQuery query='(max-width: 699px)'>
              <img src="../resources/AndroidAngledEntrySolo.png" className="image102"/>
              <br></br>
              <h3>Engage with your customers directly at any time!</h3>
            </MediaQuery>
          </div>
          <div className = "carousel3">
            <MediaQuery query='(min-width: 700px)'>
              <img src="../resources/IPhoneAngledEntry.png" className="image103"/>
            </MediaQuery>
            <MediaQuery query='(max-width: 699px)'>
              <img src="../resources/IPhoneAngledEntrySolo.png" className="image103"/>
              <br></br>
              <h3>Maintain customer loyalty - ensure to stay top of mind and relevant</h3>
            </MediaQuery>
          </div>
        </Slider>
      </div>
    );
  }
}

export default Carousel;




